.navbar {
  position: fixed;
  width: 100%;
  background-color: #FF9EC2;
  border-bottom: solid 1px #FC83AF;
  z-index: 999;
}

.navbar__items {
  list-style-type: none;
}

.navbar__item {
  color: rgba(228, 255, 47, 1);
  font-family: 'Roboto-medium';
  display: inline;
  cursor: pointer;
  margin-right: 44px;
  -webkit-transition: color 0.5s;
  transition: color 0.5s;
}

.navbar__item:hover {
  color: rgba(227, 255, 47, 0.7);
}
body {
  background-color: #FF9EC2;
}

.lines {
  position: absolute;
  bottom: 0;
}

.shitstorm {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  padding: 0;
}
.content {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.content-title-box {
  background: -webkit-linear-gradient(347deg, #fffa00, #02ff00, #00f2ff, #e3ff00);
  background: linear-gradient(103deg, #fffa00, #02ff00, #00f2ff, #e3ff00);  background-size: 800% 800%;
  -webkit-animation: TitileBoxGradientAnim 32s ease infinite;
          animation: TitileBoxGradientAnim 32s ease infinite;
  -webkit-clip-path: polygon(50% 10%, 90% 10%, 90% 19%, 80% 64%, 38% 90%, 10% 90%, 15% 44%, 33% 15%);
          clip-path: polygon(50% 10%, 90% 10%, 90% 19%, 80% 64%, 38% 90%, 10% 90%, 15% 44%, 33% 15%);
  width: 33vw;
  height: 100vh;
  -webkit-align-self: row;
          align-self: row;
  -webkit-order: 0;
          order: 0;
  text-align: center;
  display:table;

}

.content-title-box.inverted {
  -webkit-order: 1;
          order: 1;
}

.content-content-box {
  background-color: #ffdad9;
  width: 67vw;
  height: 100vh;
  -webkit-order: 1;
          order: 1;
  overflow: auto;
}

.content-content-box.inverted {
  -webkit-order: 0;
          order: 0;
}



.title-text {
  font-size: 55px;
  display:table-cell; /*acts like a table cell*/
  vertical-align: middle;
  color: #FF9EC2;
  letter-spacing: 10px;
}

.content-text {
  font-family: 'Roboto-medium';
  font-size: 30px;
}

.content-text-wrapper {
  padding: 30px;
}

@media (max-width: 1000px) {
  .content {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  
  .content-title-box {
    width: 100%;
    height: 20vh;
  }

  .content-content-box {
    width: 100%;
    height: 80vh;
  }

  .content-title-box.inverted {
    -webkit-order: 0;
            order: 0;
  }

  .content-content-box.inverted {
    -webkit-order: 1;
            order: 1;
  }

}

@-webkit-keyframes TitileBoxGradientAnim { 
  0%{background-position:0% 19%}
  50%{background-position:100% 82%}
  100%{background-position:0% 19%}
}

@keyframes TitileBoxGradientAnim { 
  0%{background-position:0% 19%}
  50%{background-position:100% 82%}
  100%{background-position:0% 19%}
}
.spanner-container {
  height: 75px;
}

.marquee {
  margin: 0 auto;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  border-bottom: solid black 5px;
  border-top: solid black 5px;
  background-color: #FF9EC2;
  position: absolute;
  height: 75px;
}

.marquee span {
  display: inline-block;
  padding-left: 100%;
  font-size: 32px;
  word-spacing: 100px;
  color: rgba(228, 255, 47, 1);
  -webkit-animation: marquee 30s linear infinite;
          animation: marquee 30s linear infinite;
  z-index:100;
  position: relative;
  top: 25%;
}

.marquee2 span {
  -webkit-animation-delay: 15s;
          animation-delay: 15s;
}

/* Make it move */
@-webkit-keyframes marquee {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
  }
}
@keyframes marquee {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
  }
}

